@import './_mixin.scss';
@import './_svg.scss';
@import './_variables.scss';

#root #wrap .content {
	// ============================== 홈 시작 ==============================
	&.main {
		grid-template-areas:
			'main-slide main-slide visual-text'
			'info rooms-tit rooms-tit'
			'info rooms-figure rooms-text'
			'info around faq'
			'news news news';
		.main-slide {
			grid-area: main-slide;
		}
		.visual-text {
			grid-area: visual-text;
			margin-bottom: 2rem;
			.sub-title {
				@include text-ellipsis(3);
				word-break: break-all;
			}
			.btn-viewall {
				display: block;
				margin-top: 1.2rem;
				text-align: center;
				.border-inner {
					padding: 2.8rem 0;
				}
				.logo {
					display: flex;
					justify-content: center;
					align-items: center;
					text-align: left;
					margin-bottom: 0.8rem;
					gap: 0.4rem;
					img {
						width: 36px;
					}
					div {
						p {
							font-family: 'ChosunSm', serif;
						}
					}
				}
				span {
					font-family: 'ChosunSm', serif;
					font-size: 1.8rem;
				}
			}
			.progress {
				margin-bottom: 2rem;
			}
			@include mobile {
				.sub-title {
					@include text-ellipsis(1);
				}
			}
		}

		.rooms-tit {
			grid-area: rooms-tit;
			.tab-title {
				display: flex;
				gap: 1rem;
				align-items: flex-end;
				flex-wrap: wrap;
				height: 2.8rem;
				button {
					color: #999;
					transition-duration: 0.5s;
					font-size: 2rem;
					&.active {
						color: #000;
						font-size: 2.4rem;
					}
					span {
						font-size: 1.8rem;
					}
				}
			}
			@include mobile {
				.tab-title {
					height: 5.6rem;
					gap: 0;
					align-items: center;
					button {
						flex-basis: 50%;
						font-size: 1.8rem;
						height: 2.8rem;
						span {
							display: none;
						}
					}
				}
			}
		}
		.rooms-figure {
			grid-area: rooms-figure;
			position: relative;
			.auto-height-figure {
				position: absolute;
				left: 0;
				top: 0;
				opacity: 0;
				width: 100%;
				transition-duration: 0.5s;
				&.active {
					opacity: 1;
				}
			}
			@include mobile {
				aspect-ratio: 16/9;
				margin-top: 1.2rem;
			}
		}
		.rooms-text {
			grid-area: rooms-text;
			position: relative;
			margin-bottom: 2rem;
			> div {
				margin-top: 1.2rem;
				&:first-child {
					margin-top: 0;
				}
				.text {
					margin-top: 0.8rem;
				}
			}
			.control {
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding: 2rem;
				border: 0.6rem double #000;
				font-family: 'ChosunLo', sans-serif;
				font-weight: 500;
				button {
					width: 2rem;
					height: 2rem;
					font-family: 'ChosunLo', sans-serif;
					font-weight: 500;
				}
			}
			@include tablet {
				> div .text {
					@include text-ellipsis(4);
				}
			}
			@include mobile {
				margin-top: 3.2rem;
				.control {
					position: absolute;
					left: 0;
					top: 0;
					width: 100%;
					margin-top: 0;
					padding: 1.6rem;
				}
				> div:first-child {
					padding-top: 7.6rem;
				}
			}
		}

		.info {
			grid-area: info;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			gap: 2.4rem;
			.info-reservation {
			}
			.info-waytocome {
				img {
					margin-bottom: 1.2rem;
				}
			}
		}

		.around {
			grid-area: around;
			&-list {
				margin-top: 1.2rem;
				display: flex;
				gap: 1.2rem;
				> li {
					flex: 1;
					.around-text {
						margin-top: 0.4rem;
						h4 {
							@include text-ellipsis(1);
							margin-bottom: 0.4rem;
						}
						.source {
							color: #666;
							text-align: right;
							margin: 0.2rem;
							position: relative;
							font-size: 1.4rem;
						}
						.text {
							@include text-ellipsis(2);
						}
					}
				}
			}
		}

		.faq {
			grid-area: faq;
			h4 {
				word-break: break-all;
			}
		}

		.news {
			grid-area: news;
			display: flex;
			flex-wrap: wrap;
			gap: 4rem;
			border-bottom: 0.6rem double #000;
			padding: 2rem 0;
			> button {
				position: relative;
				flex: 1;
				display: flex;
				gap: 1rem;
				text-align: left;
				&:not(:last-child)::after {
					content: '';
					display: block;
					width: 0.2rem;
					height: 110%;
					position: absolute;
					right: -2rem;
					top: -5%;
					background-color: #000;
				}
				.thumb {
					@include img-crop(16rem, 16rem);
				}
				.cont {
					flex: 1;
					height: 100%;
					display: flex;
					flex-direction: column;
					justify-content: space-evenly;
					.tit {
						@include text-ellipsis(2);
					}
					.text {
						font-size: 1.4rem;
						@include text-ellipsis(5);
					}
				}
			}
		}
	}
	// ============================== 홈  끝  ==============================

	// ============================== 펜션소개 시작 ==============================
	&.about {
		grid-template-areas:
			'gallery gallery gallery'
			'visual visual visual'
			'waytocome waytocome facilities';

		.gallery {
			grid-area: gallery;
			position: relative;
			.btn-origin {
				position: absolute;
				right: 0.8rem;
				top: 0.8rem;
				z-index: 9;
				width: 12rem;
				height: 8rem;
				border: 0.6rem double #000;
				background: #f3f3f3;
				&::after {
					user-select: none;
					pointer-events: none;
					content: '';
					display: block;
					width: 100%;
					height: 100%;
					position: absolute;
					left: 0;
					top: 0;
					background: url(../img/paper.png) 0 0/ 100% auto repeat-y;
				}
			}
			&-images {
				height: 80%;
				img {
					aspect-ratio: 16/7;
					object-fit: cover;
				}
				.swiper-button-prev,
				.swiper-button-next {
					bottom: calc(50% - 28px);
				}
			}
			&-thumbs {
				position: relative;
				height: 20%;
				img {
					height: 100%;
					object-fit: cover;
				}
			}
		}

		.visual {
			grid-area: visual;
			text-align: center;
			padding: 6rem 0 3.6rem 0;
		}

		.waytocome {
			grid-area: waytocome;
			.sub-title {
				margin-top: 1.2rem;
			}
		}

		.facilities {
			grid-area: facilities;
			.facility-list {
				display: grid;
				height: calc(100% - 50px);
				grid-template-columns: repeat(2, 1fr);
				text-align: center;
				align-items: center;
				align-content: stretch;
				li {
					justify-self: center;
					i {
						display: block;
						margin: 0 auto;
						width: 40px;
						height: 40px;
						border: 2px solid #000;
						border-radius: 50%;
						margin-bottom: 0.8rem;
					}
					p {
						font-size: 1.4rem;
					}
				}
			}
		}
	}
	// ============================== 펜션소개  끝  ==============================

	// ============================== 객실소개 시작 ==============================
	&.rooms {
		grid-template-columns: repeat(4, 1fr);
		.detail {
			h2 {
				text-align: left;
				.h3 {
					color: #666;
				}
			}
			button {
				.roomData-figure {
					position: relative;
					overflow: hidden;
					img {
						transition-duration: 0.5s;
					}
					img:nth-child(2) {
						position: absolute;
						left: 100%;
						top: 0;
					}
				}
				&:hover {
					.roomData-figure {
						img {
							transform: translateX(-100%);
						}
					}
				}
				figcaption {
					text-align: left;
				}
			}
		}
		.map {
			grid-column: 1 /5;
			position: relative;
			.btn-modal button,
			dd button i {
				display: block;
				width: 26px;
				line-height: 26px;
				text-align: center;
				border-radius: 50%;
				font-size: 1.5rem;
				font-family: 'ChosunLo', sans-serif;
				&:hover {
					&:not(button i)::before,
					&:not(button i)::after {
						content: '';
						display: block;
						width: 30px;
						height: 30px;
						position: absolute;
						left: calc(50% - 15px);
						top: calc(50% - 15px);
						border: 1px solid #fff;
						border-radius: 50%;
						box-sizing: border-box;
						opacity: 0;
						@keyframes hover {
							from {
								transform: scale(1);
								opacity: 0;
							}
							10% {
								opacity: 1;
							}
							to {
								transform: scale(2);
								opacity: 0;
							}
						}
					}
					&:not(button i)::before {
						animation: hover 1s linear 0s infinite;
					}
					&:not(button i)::after {
						animation: hover 1s linear 0.3s infinite;
					}
				}
				&[class^='s'] {
					background-color: #a9e3ee;
				}
				&[class^='c'] {
					background-color: #dddddd;
				}
				&[class^='m'] {
					background-color: #ff7f80;
				}
				&[class^='v'] {
					background-color: #b0c4e9;
				}
				&[class^='p'],
				&.t,
				&.o {
					background-color: #000;
					color: #fff;
				}
			}
			.info-list {
				position: absolute;
				left: 20px;
				bottom: 40px;
				width: 260px;
				padding: 6px;
				background-color: #ffffffd0;
				.info {
					dt {
						h4 {
							font-size: 1.6rem;
							padding: 6px;
							border-radius: 15.5px;
							background-color: #000000af;
							text-align: center;
							color: #fff;
							margin-top: 4px;
						}
					}
					dd {
						display: flex;
						flex-wrap: wrap;
						> * {
							width: 50%;
						}
						button {
							display: flex;
							align-items: center;
							gap: 4px;
							margin-top: 4px;
						}
					}
				}
			}
			.btn-modal {
				button {
					position: absolute;
					transition-duration: 0.5s;
					&.s1 {
						left: 30%;
						top: 68%;
					}
					&.c1 {
						left: 33.5%;
						top: 66.5%;
					}
					&.c2 {
						left: 37%;
						top: 65%;
					}
					&.s2 {
						left: 40.5%;
						top: 63.5%;
					}

					&.v1 {
						left: 31.75%;
						top: 73%;
					}
					&.v2 {
						left: 35.25%;
						top: 71.5%;
					}
					&.m {
						left: 38.75%;
						top: 70%;
					}

					&.s3 {
						left: 57%;
						top: 53%;
					}
					&.c3 {
						left: 63.33%;
						top: 49.66%;
					}
					&.c4 {
						left: 69.66%;
						top: 46.33%;
					}
					&.s4 {
						left: 76%;
						top: 43%;
					}

					&.p1 {
						left: 27%;
						top: 72%;
					}
					&.p2 {
						left: 47%;
						top: 70%;
					}
					&.t {
						left: 51.5%;
						top: 68.5%;
					}
					&.o {
						left: 62%;
						top: 62%;
					}
					&.p3 {
						left: 92%;
						top: 47%;
					}
				}
			}
		}
	}
	// ============================== 객실소개  끝  ==============================

	// ============================== 펜션소식 시작 ==============================
	&.news {
		grid-template-columns: repeat(4, 1fr);
		button {
			text-align: left;
			.news-figure {
				margin-bottom: 1.2rem;
			}
			h3 {
				@include text-ellipsis(2);
				height: 56px;
			}
			.date {
				color: #666;
				text-align: right;
				margin: 0.2rem;
				position: relative;
				font-size: 1.4rem;
				&::before {
					content: '';
					display: block;
					width: calc(100% - 7rem);
					height: 1px;
					border-top: 1px dashed #000;
					position: absolute;
					left: 0;
					top: calc(50% - 0.05rem);
				}
			}
			.text {
				@include text-ellipsis(5);
			}
		}
	}
	// ============================== 펜션소식  끝  ==============================

	// ============================== 예약하기 시작 ==============================
	&.reservation {
		grid-template-columns: 1fr 2fr;
		grid-template-areas:
			'caution info'
			'penalty penalty'
			'shortcut shortcut';
		.caution {
			grid-area: caution;
		}
		.info {
			grid-area: info;
		}
		.penalty {
			grid-area: penalty;
		}
		.shortcut {
			grid-area: shortcut;
			.designed-border {
				display: block;
				text-align: center;
				.border-inner {
					padding: 6rem 2rem;
				}
			}
		}
	}
	// ============================== 예약하기  끝  ==============================

	// ============================== 주변소개 시작 ==============================
	&.around {
		grid-template-columns: repeat(3, 1fr);
		button {
			text-align: left;
			figure {
				margin-bottom: 1.2rem;
			}
			h3 {
				@include text-ellipsis(1);
			}
			.text {
				margin: 0.4rem 0;
			}
			.source {
				color: #666;
				text-align: right;
				position: relative;
				font-size: 1.4rem;
			}
			.text {
				@include text-ellipsis(6);
			}
		}
	}
	// ============================== 주변소개  끝  ==============================

	// ============================== 자주하는질문 시작 ==============================
	&.faq {
		grid-template-areas: 'sort faq-list faq-list';
		.sort {
			grid-area: sort;
			button {
				display: block;
				color: #666;
				margin-bottom: 0.8rem;
				&.active {
					color: #000;
				}
				span {
					font-size: 1.8rem;
				}
			}
		}
		.faq-list {
			grid-area: faq-list;
			.title {
				display: flex;
				justify-content: space-between;
				align-items: flex-end;
				.btns {
					gap: 0.8rem;
					button {
						font-family: 'ChosunSm', serif;
						color: #666;
						&:last-child {
							margin-left: 0.8rem;
						}
					}
				}
			}
		}
	}
	// ============================== 자주하는질문  끝  ==============================
}
