$default: #000;
$primary: #fff;

$default-color: #f2f232;

$c1: darken($default-color, 10%);
$c2: darken($default-color, 5%);
$c3: $default-color;
$c4: lighten($default-color, 5%);
$c5: lighten($default-color, 10%);

$ac1: rgba($c1, 0.5);
$ac2: rgba($c2, 0.5);
$ac3: rgba($c3, 0.5);
$ac4: rgba($c4, 0.5);
$ac5: rgba($c5, 0.5);
